h1 {
  font-size: 72px;
  color: rgb(59, 59, 59);
}

.App {
  text-align: center;
  font-family: 'Karla';
}

.App-logo {
  height: 7vmin;
  pointer-events: none;
  margin-bottom: 4%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite linear 2s alternate;
                
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(180deg);
  }
}